<template>
  <div class="hello">
    <model-gltf
      src="https://openseauserdata.com/files/035dba1e7f2c4e0f1d4575f7341997b3.gltf"
    />
    <a href="https://testnets.opensea.io/collection/hulk-m17xuszta8">
      오픈씨 바로가기
    </a>
    <br>
    <span id = "csstest" class ="topTitle">
      안녕하세요 창작모꼬지 NFT 프로그래밍반 손효기
    </span>
    <div v-if="isInstallMetaMask == true">
      메타 마스크 설치됨
      <!-- 메타마스크 연결 버튼 -->
      <div v-if="currentAddress == null">
        <button v-on:click="connectMetaMask">메타 마스크 연결</button>
      </div>

      <div v-if="currentAddress != null">
        현재 계정 주소 : {{currentAddress}}
        <!-- 컨트랙트 연결 버튼 -->
        <div v-if="connectedContract == null">
          <button v-on:click="connectContract">컨트랙트 연결</button>
        </div>
        <div v-else>
          <!-- ownerOf 함수를 호출해보자 -->
          <div class="function">
            <div class="title">
              ownerOf
            </div>

            <div class="parameters">
              <span class="parameterName">tokenId</span>
              <input class="parameterInput" type="text" placeholder="tokenId" v-model="ownerOf_tokenId" />
            </div>

            <div class="buttons">
              <button class="callButton" v-on:click="ownerOf">call</button>
            </div>

            <div class="result" v-if="ownerOf_result != null">
              ownerOf : {{ownerOf_result}}
            </div>
          </div>

          <!-- balanceOf 구현-->
          <div class="function">
            <div class="title">
              balanceOf
            </div>

            <div class="parameters">
              <span class="parameterName">owner</span>
              <input class="parameterInput" type="text" placeholder="owner" v-model="balanceOf_owner" />
            </div>
            <div class="buttons">
              <button class="callButton" v-on:click="balanceOf">call</button>
            </div>
            <div class="result" v-if="balanceOf_result != null">
              balanceOf : {{balanceOf_result}}
            </div>
          </div>

          <!-- mint 구현-->
          <div class="function">
            <div class="title">
              mint
            </div>

            <div class="parameters">
              <span class="parameterName">to</span>
              <input class="parameterInput" type="text" placeholder="to" v-model="mint_to" />
            </div>

            <div class="parameters">
              <span class="parameterName">tokenId</span>
              <input class="parameterInput" type="text" placeholder="tokenId" v-model="mint_tokenId" />
            </div>

            <div class="parameters">
              <span class="parameterName">uri</span>
              <input class="parameterInput" type="text" placeholder="uri" v-model="mint_uri" />
            </div>

            <div class="buttons">
              <button class="callButton" v-on:click="mint">call</button>
            </div>
            <div class="result" v-if="mint_result != null">
              mint : {{mint_result}}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else> 메타 마스크 설치 안됨, 설치 해주세요</div>
  </div>
</template>

<script>
import {ethers} from 'ethers';
import abi from '../assets/abi.json';
import { ModelGltf } from 'vue-3d-model';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components:{
    ModelGltf
  },
  data() {
    return {
     isInstallMetaMask : window.ethereum != null 
     && window.ethereum.isMetaMask == true,     

     currentAddress : null,

     connectedContract : null,

     // ownerOf에 필요한 변수
     ownerOf_tokenId : null,
     ownerOf_result : null,

     // balanceOf에 필요한 변수
     balanceOf_owner : null,
     balanceOf_result : null,

     // mint에 필요한 변수
     mint_to : null,
     mint_tokenId : null,
     mint_uri : null,
     mint_result : null,
    }
  },
  methods:{
    async connectMetaMask()
    {
      console.log("connectMetaMask 호출");
      var account = await window.ethereum.request( { method:'eth_requestAccounts'});
      if ( account.length > 0 )
      {
        this.currentAddress = account[0];
        console.log(this.currentAddress);
      }
      else
      {
        console.log("계정이 없네!");
      }
    },
    async connectContract()
    {
      console.log("connectContract 호출");
      
      var contractAddress = "0xc3565f891df4a703146c9ddb8a7a804ecc5e6526";
      var provider = new ethers.providers.Web3Provider(window.ethereum);

      var tempSigner = provider.getSigner();
      var connectedContract = new ethers.Contract( contractAddress, abi, tempSigner );
      console.log(connectedContract);
      this.connectedContract = connectedContract;
    },
    async ownerOf()
    {
      console.log("ownerOf 호출");
      var ownerAddress = await this.connectedContract.ownerOf(this.ownerOf_tokenId);
      console.log(ownerAddress);
      this.ownerOf_result = ownerAddress;
    },
    async balanceOf()
    {
      console.log("balanceOf 호출");
      var count = await this.connectedContract.balanceOf(this.balanceOf_owner);
      console.log(count);
      this.balanceOf_result = count;
    },
    async mint()
    {
      console.log("mint 호출");
      try
      {
        var transaction = await this.connectedContract.mint(
                                  this.mint_to, 
                                  this.mint_tokenId, 
                                  this.mint_uri);
        console.log(transaction);
        this.mint_result = "생성 대기중";
        await transaction.wait();
        
        this.mint_result = transaction;
      }
      catch(err)
      {
        console.log(err);
        this.mint_result = err;
      }
    }
  }
}
</script>

